@import url(https://fonts.googleapis.com/css2?family=Alegreya:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya:wght@800&family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya:wght@800&family=Kanit:wght@800&family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya:wght@800&family=Changa+One&family=Kanit:wght@800&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: "DINPro Medium";
    font-style: normal;
    font-weight: normal;
    src: local("DINPro Medium"), url(/static/media/DINPro-Medium.16a72c6a.otf) format("opentype");
  }
  
  @font-face {
    font-family: "DINPro Bold";
    font-style: normal;
    font-weight: normal;
    src: local("DINPro Bold"), url(/static/media/DINPro-Bold.e18c1d11.otf) format("opentype");
  }
  
  @font-face {
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 400;
    src: local("DINPro Bold"), url(/static/media/OpenSans-Regular.1b0809d5.ttf) format("truetype");
  }
  
  @font-face {
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 500;
    src: local("DINPro Bold"), url(/static/media/OpenSans-Bold.dbb97fd9.ttf) format("truetype");
  }
